img {
  height: 190px;
}

.about-section {
  padding: 80px 0;
  background-color: #f9f9f9;
}

.footer {
  color: black;
}

.social-icons {
  margin-bottom: 10px;
}

.social-icon {
  color: black;
  margin-right: 10px;
  transition: color 0.3s ease-in-out;
}

.social-icon:hover {
  color: #007bff;
}

.home-section {
  padding: 400px 0;
  text-align: center;
  background-image: url("https://png.pngtree.com/thumb_back/fh260/background/20210630/pngtree-pink-purple-gold-rim-watercolor-ink-background-image_735257.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.section-title {
  font-size: 48px;
  font-weight: bold;
  color: #333;
  margin-bottom: 20px;
  font-family: Shadow, ui-rounded;
  white-space: nowrap;
  overflow: hidden;
  animation: typing 3s steps(40), blink-caret 0.75s step-end infinite;
  /* Replace with desired font family */
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #fff;
  }
}

.typing-cursor {
  animation: blink-caret 0.75s step-end infinite;
}

.section-description {
  font-size: 24px;
  line-height: 1.5;
  color: #666;
  margin-bottom: 30px;
  font-family: 'Arial', sans-serif;
  /* Replace with desired font family */
}

.btn-primary {
  font-size: 20px;
  font-weight: bold;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  text-decoration: none;
  transition: background-color 0.3s ease;
  font-family: 'Arial', sans-serif;
  /* Replace with desired font family */
}

.btn-primary:hover {
  background-color: #0056b3;
}

.profile-image img {
  width: 200px;
  height: auto;
  border-radius: 50%;
  margin-top: 30px;
}

@media (max-width: 767px) {
  .section-title {
    font-size: 36px;
  }

  .section-description {
    font-size: 20px;
  }
}

